@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");
body {
  font-family: "Roboto Condensed", sans-serif;
}

:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;
  --primary-red-hover: #f33501;
}
/* -----------------------Navbar ------------------------- */
nav {
  z-index: 2;
}
.logo {
  width: 4rem;
  height: 4rem;
}

/* ------------ Menu- Links ------------------- */
nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}
nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17rem;
}
@media(max-width:768px){
  .navbar-brand {
    max-width: 7rem;
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus,
.navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid var(--primary-orange);
}
.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid var(--primary-orange);
}

/* ----------------HEADER-------------- */

.header-wrapper {
  position: relative;
  background: url("wall-background.jpg") no-repeat;
  height: 87.2vh;
  background-position: center;
  background-size: cover;
  font-family: "Roboto Condensed", sans-serif;
}

.main-info {
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.main-info  h1 {
  color: var(--primary-orange);
}

/* ----------TYPED TEXT ------------- */

.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}
@media(max-width:768px){

  .main-info  h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}
/* -----------Header Main Button Offer --------------- */
.btn-main-offer {
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  text-decoration: none;
  z-index: 1;
}

.btn-main-offer:hover {
  background-color: var(--primary-red-hover);
  color: var(--primary-white);
}

/* -------------------PARTICLES------------- */
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}
